import React, { useEffect,useState } from 'react'
import './Banner.css'
import axios from "../../axios"
import {MoviesCollectionLinks,ImageBaseUrl} from "../../Links/Links"
function Banner() {
    const [movie,setMovie]=useState([])
    function truncate(str,n){
        return str?.length >n ? str.substr(0, n-1) + "..." : str;
    }
useEffect(() => {
    axios.get(MoviesCollectionLinks.originals).then((response)=>{
        setMovie(response.data.results[Math.floor(Math.random() * response.data.results.length - 0)])
    })
}, [])

    return (
       <div className='contaner'>
           <div className='backDrop' style={{
              
                backgroundImage: `url(${movie ? ImageBaseUrl+ movie.backdrop_path :""} )`}}>
                    <div className="bannerContents">
     
                        <h1 className="title">{movie.name}</h1>
                            <div className="buttonContener">
                            <button className="button">Play</button>
                            <button className="button">My List</button>
                            </div>
                        <h3 className='movieDescription'>{truncate(movie?.overview,150)}</h3>
                    </div>
                 <div className='faidbottom'></div>
           </div>
           </div>
        
    )
}

export default Banner
