import React, { useEffect, useContext } from "react";
import "./Profile.css";
import { Avathar } from "../../Links/Links";
import Navbar from "../Navbar/Navbar";
import { AuthContext, FirebaseContext } from "../../Stor/FirebaseContext";
import { useHistory } from "react-router-dom";
function Profile() {
  const history = useHistory();
  const { user } = useContext(AuthContext);
  const { firebase } = useContext(FirebaseContext);
  const Logout = () => {
    firebase.auth().signOut();
    history.push("/");
  };
  return (
    <div className="Profile">
      <Navbar />
      <div className="profileContener">
        <h1>Profile</h1>
        <div className="secondRowContener">
          <div className="lhContener">
            <img src={Avathar} alt="" />
          </div>
          <div className="rhContener">
            <h3>{user ? user.displayName : "nouser"}</h3>
            <button onClick={() => Logout()}>Sign Out</button>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Profile;
