import React, { useState, useContext } from "react";
import "./Sigup.css";
import { NavBarLink } from "../../Links/Links";
import { useHistory } from "react-router-dom";
import { FirebaseContext } from "../../Stor/FirebaseContext";
function Login() {
  const history = useHistory();
  const { firebase } = useContext(FirebaseContext);
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const handilSubint = (e) => {
    e.preventDefault();
    console.log(email);
    firebase
      .auth()
      .signInWithEmailAndPassword(email, password)
      .then(() => {
        history.push("/");
      })
      .catch((error) => {
        alert(error.message);
      });
  };

  return (
    <div>
      <div
        className="BackDropClass"
        style={{ backgroundImage: "url(/Images/backdrop.jpg)" }}
      >
        <img
          className="ImageForLogo"
          style={{ cursor: "pointer" }}
          onClick={() => history.push("/")}
          src={NavBarLink}
          alt=""
        />

        <div className="SignupContener">
          <form onSubmit={handilSubint}>
            <h1>Sign In</h1>
            <input
              type="email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              placeholder="Emil"
            />
            <input
              type="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              placeholder="Password"
            />
            <button>Sign In</button>
            <h3>
              {" "}
              <span style={{ color: "grey" }}>New to Netflix?</span>{" "}
              <span
                onClick={() => {
                  history.push("/signup");
                }}
                style={{ cursor: "pointer" }}
              >
                Sign Up
              </span>{" "}
            </h3>
          </form>
        </div>
      </div>
    </div>
  );
}

export default Login;
