import React, { useState, useContext } from "react";
import "./Sigup.css";
import { NavBarLink } from "../../Links/Links";
import { useHistory } from "react-router-dom";
import { FirebaseContext } from "../../Stor/FirebaseContext";

function Signup() {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [name, setName] = useState("");
  const { firebase } = useContext(FirebaseContext);
  const history = useHistory();
  const handilSubmit = (e) => {
    e.preventDefault();
    firebase
      .auth()
      .createUserWithEmailAndPassword(email, password)
      .then((result) => {
        result.user.updateProfile({ displayName: name }).then(() => {
          firebase
            .firestore()
            .collection("users")
            .add({
              id: result.user.uid,
              name: name,
            })
            .then(() => {
              history.push("/signin");
            });
        });
      });
  };
  return (
    <div
      className="BackDropClass"
      style={{ backgroundImage: "url(/Images/backdrop.jpg)" }}
    >
      <img
        className="ImageForLogo"
        style={{ cursor: "pointer" }}
        onClick={() => history.push("/")}
        src={NavBarLink}
        alt=""
      />

      <div className="SignupContener">
        <form onSubmit={handilSubmit}>
          <h1>Sign Up</h1>
          <input
            type="text"
            value={name}
            onChange={(e) => setName(e.target.value)}
            placeholder="Name"
          />
          <input
            type="email"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            placeholder="Emil"
          />

          <input
            type="password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            placeholder="Password"
          />
          <button>Sign Up</button>
          <h3> New to Netflix?</h3>
        </form>
      </div>
    </div>
  );
}

export default Signup;
