import "./Row.css";
import React, { useEffect, useState } from "react";
import axios from "../../axios";
import YouTube from "react-youtube";
import { ImageBaseUrl, API_KEY } from "../../Links/Links";

function Row(props) {
  const [movie, setMovie] = useState([]);
  const [urlId, setUrlId] = useState();
  useEffect(() => {
    axios.get(props.urls).then((response) => {
      setMovie(response.data.results);
    });
  }, []);
  const youtubId = (id) => {
    if (urlId) {
      setUrlId("");
    } else {
      axios
        .get(`/movie/${id}/videos?api_key=${API_KEY}&language=en-US`)
        .then((obj) => {
          if (obj.data.results.length !== 0) {
            setUrlId(obj.data.results[0]);
          } else {
            alert("Sorry This Movie Trailers is not Available Right Now");
          }
        });
    }
  };
  const opts = {
    height: "390",
    width: "100%",
    playerVars: {
      autoplay: 1,
    },
  };

  return (
    <div className="row">
      <h1> {props.title}</h1>
      <div className="posters">
        {movie.map((obj) => {
          return (
            <img
              onClick={() => youtubId(obj.id)}
              key={obj.id}
              className={props.isLarge ? "poster" : "smallposter"}
              src={`${
                props.isLarge
                  ? ImageBaseUrl + obj.poster_path
                  : ImageBaseUrl + obj.backdrop_path
              }`}
              alt={obj.name}
            />
          );
        })}
      </div>
      {urlId && <YouTube opts={opts} videoId={urlId.key} />}
    </div>
  );
}

export default Row;
